/* src/pages/Home.css */

.home {
  text-align: center;
  padding: 2rem 1rem;
}

.hero {
  background-image: url("../assets/images/photo-1.avif");
  background-size: cover;
  /* background-position: center; */
  color: #fff;
  padding: 5rem 1rem;
}

.hero h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
}

.hero p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.shop-now {
  background-color: #e91e63;
  color: #fff;
  padding: 0.75rem 2rem;
  font-size: 1.25rem;
  border-radius: 5px;
}
.shop-now:hover {
  background-color: #100f0f;
  color: white;
}
.collections {
  margin-top: 3rem;
}

.collections h2 {
  font-size: 2rem;
  margin-bottom: 2rem;
}

.collection-list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.collection-item {
  width: 30%;
  margin-bottom: 2rem;
}

.collection-item img {
  width: 100%;
  border-radius: 5px;
}

.collection-item p {
  font-size: 1.25rem;
  margin-top: 0.5rem;
}
