/* src/pages/Login.css */
.login-page {
  display: flex;
  justify-content: space-between;
  height: 87vh;
}
.form{
  margin:5rem 10rem;
  padding: 2rem;
  width: 300px;
}

.img {
  width: 50%;
  background-image: url("../assets/images/photo-1.avif");
  background-repeat: no-repeat;
  background-size: cover;
  margin: 0;
}

.form-group {
  width: 100%;
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
}

.form-group input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 2px;
}

button {
  padding: 0.5rem 1rem;
  border: none;
  background-color: #007bff;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 5px;
}

button:hover {
  background-color: #0056b3;
}
