/* src/pages/Checkout.css */
.checkout {
  padding: 2rem;
  max-width: 600px;
  margin: 0 auto;
}

.checkout h1,
.checkout h2 {
  font-size: 2rem;
  margin-bottom: 1rem;
}

.checkout-summary {
  background-color: #f8f8f8;
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 2rem;
}

.checkout-summary ul {
  list-style: none;
  padding: 0;
}

.checkout-summary li {
  border-bottom: 1px solid #ddd;
  padding: 0.5rem 0;
}

.checkout-summary h3 {
  margin-top: 1rem;
}

.form-group {
  margin-bottom: 1rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
}

.form-group input[type="text"] {
  width: 100%;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.checkout-button {
  background-color: #e91e63;
  color: #fff;
  border: none;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 1rem;
  width: 100%;
}

.checkout-button:hover {
  background-color: #c2185b;
}
