/* src/components/Trends.css */
.trends {
  padding: 2rem;
}

.trends h2 {
  margin-bottom: 1rem;
  font-size: 2rem;
  font-weight: bold;
}

.trends-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  gap: 1rem;
}

.trend-item {
  text-align: center;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  background-color: #f9f9f9;
}

.trend-item img {
  width: 100%;
  height: auto;
  border-radius: 8px;
  margin-bottom: 0.5rem;
}

.trend-item p {
  margin: 0.5rem 0;
}
