
.products-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1rem;
  padding: 1rem 5rem;
}

.product-card {
  border: 1px solid #ddd;
  text-align: center;
  background-color: #fff;
  transition: 0.3s ease;

}
.product-card:hover{
  transform: scale(1.04);
  box-shadow: 0 0 4px 2px rgb(202, 202, 202);
}

.product-img {
  width: 100%;
  height: auto;
  margin: 0;
  margin-bottom: 1rem;
  padding: 0;
}

.product-price {
  color: #007bff;
  font-weight: bold;
} 
.desc{
  padding: 0 1rem;
  text-align: justify;
}