/* src/components/SearchBar/SearchBar.css */
.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar input {
  width: 350px;
  padding: 0.5rem;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-right: none;
  outline: none;
  border-radius: 4px 0 0 4px;
}

.search-bar .seaBtn {
  padding: 0.5rem;
  font-size: 1rem;
  margin: 0;
  background-color: #e91e63;
  color: #fff;
  border: 1px solid #e91e63;
  border-left: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
}

.search-bar .seaBtn:hover {
  background-color: #ff0066;
}
